<template lang="pug">
    aside#side-menu
        a(@click="show = !show")
            i.fas.fa-times(v-if="show")
            i.fas.fa-bars(v-if="!show")
        transition(name="fade")
            .content(v-if="show")
                ul
                    li.first
                        a(@click="showGallery('F')") Muebles
                    li 
                        a(@click="showGallery('C')") Cocina
                    li 
                        a(@click="showGallery('E')") Electnicos
                    li.last
                        a(@click="showGallery('V')") Varios
</template>

<script>
export default {
    data () {
        return {
            show: false
        }
    },
    methods: {
        showGallery (type) {
            this.$bus.$emit('view-gallery', type);
            this.show = false;
        }
    }
}
</script>




