<template lang="pug">
    .item-viewer(v-if="show")
        span.close(@click="close") 
        .slider
            .slider-inner(:style="{ width: (100 * product.images.length) + '%', left: left }")
                figure.image-container
                    img(v-for="(name,index) in product.images", :src="getImgUrl(name)", :style="{ width: (100 / product.images.length) + '%' }")
            span.left(@click="prev()", v-show="idx > 0")
                i.fas.fa-angle-left
            span.right(@click="next()", v-show="idx < (product.images.length-1)")
                i.fas.fa-angle-right
        span.indicators
            i.fas.fa-circle(v-for="(name, index) in product.images", 
                :class="index == idx ? 'selected' : '' ",
                @click="go(index)")
</template>

<script>
export default {
    data () {
        return {
            show: false,
            product: {},
            idx: 0,
            left: '0'
        }
    },
    methods: {
        close () {
            this.show = false
        },
        getImgUrl (name) {
            return `${HOMEURL}img/store/${this.product.type}/${this.product.id}/${name}.jpg`;
        },
        prev () {
          this.idx--
          if (this.idx < 0) this.idx = 0
          this.move()
        },
        next () {
            this.idx++
            if (this.idx >= this.product.images.length) this.idx = this.product.images.length
            this.move()
        },
        go(i){
            this.idx=i
            this.move()
        },
        move () {
            if ( this.idx === 0) this.left = '0'
            else if ( this.idx > 0) this.left = `${-100 * this.idx }%`
        }
    },
    created () {
        this.$bus.$on('view-product', (product)=> {
            this.product = product
            this.show = true
            this.idx = 0,
            this.left = '0'
        })
    }
}
</script>





