import 'bootstrap'
import Vue from 'vue'
import App from '@/App.vue'
import EventBus from '@/plugins/event-bus'
import routes from '@/routes'
import jQuery from 'jquery'
import VueRouter from 'vue-router';

Vue.use(EventBus)
Vue.use(VueRouter)
global.$ = jQuery //Use it just to handler the bootstrap events

const router = new VueRouter({
  routes,
  mode: 'history'
})

new Vue({
  el: '#page',
  render: h => h(App),
  router
})



